*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }
  @media only screen and (max-width: 1800px) {
    html {
      font-size: 75%; } }
  @media only screen and (max-width: 1200px) {
    html {
      font-size: 56.25%; } }
  @media only screen and (max-width: 900px) {
    html {
      font-size: 50%; } }

body {
  font-family: "Montserrat", sans-serif;
  color: #f9f8f6;
  font-weight: 300;
  line-height: 1.6; }

#root {
  display: grid;
  grid-template-rows: 1fr;
  background-color: #f9f8f6; }
  @media only screen and (max-width: 430px) {
    #root {
      grid-template-columns: initial; } }

.App {
  grid-column: 1/3; }

.header {
  grid-column: 2; }
  @media only screen and (max-width: 500px) {
    .header {
      padding: 0rem 0rem; } }

a {
  text-decoration: none;
  color: black; }

.header {
  display: grid;
  grid-template-columns: min-content 1fr 0.5fr;
  align-items: center;
  color: white;
  font-size: 2rem;
  background-color: #d22030;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 999; }
  @media only screen and (max-width: 700px) {
    .header {
      grid-template-columns: 1fr;
      justify-items: center; } }
  @media only screen and (max-width: 500px) {
    .header {
      display: initial;
      position: initial; } }
  .header__left--list, .header__right--list {
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    font-size: 1.5rem;
    grid-template-columns: min-content auto;
    align-items: center; }
  .header__left--item, .header__right--item {
    margin-right: 1rem;
    font-weight: 500; }
    .header__left--item:hover, .header__right--item:hover {
      opacity: 0.5;
      cursor: pointer; }
  .header__logo {
    margin-left: 2rem;
    height: 7rem; }
    @media only screen and (max-width: 500px) {
      .header__logo {
        display: flex;
        justify-content: center;
        background-color: #d22030;
        margin-left: 0; } }
    .header__logo img {
      height: 100%;
      padding: 1rem; }
      @media only screen and (max-width: 500px) {
        .header__logo img {
          height: 7rem;
          padding: 1rem; } }
  .header__left--list, .header__right--list {
    background-color: #d22030; }

.button {
  transitoin: all 0.2s; }
  .button__signup, .button__login {
    color: white;
    padding: 1rem 2rem 1rem 2rem;
    border-radius: 3rem;
    transition: all 0.2s; }
    .button__signup:hover, .button__login:hover {
      opacity: 0.5;
      cursor: pointer; }
  .button__shop-now--full, .button__share--full {
    display: grid;
    grid-template-rows: min-content min-content;
    justify-content: center;
    box-shadow: 1px 1px 19px 1px #888888;
    width: 30rem;
    border-radius: 5rem; }
  .button__share--full {
    color: #d22030;
    background-color: white; }
  .button__name {
    font-size: 3.5rem;
    font-weight: 500; }
  .button:hover {
    cursor: pointer;
    opacity: 0.5; }
  .button__normal {
    padding: 0.75rem 2rem;
    border-radius: 2rem;
    background-color: #d22030;
    color: white; }
    .button__normal--share {
      border: 1px solid #d22030;
      background-color: white;
      color: #d22030; }

