  .banner__header, .banner__recent-supporters, .banner__container--progressAndButtons {
    background-image: linear-gradient(to right bottom, rgba(210, 32, 48, 0.8), rgba(210, 32, 48, 0.8)); }
  .banner__header {
    font-size: 3rem;
