    .banner__buttons--container {
      background-color: #d22030;
      display: grid;
      justify-items: center;
      border-radius: 3rem;
      font-size: 2.5rem;
      text-transform: uppercase;
      border: 1px solid white; }
      .banner__buttons--container:hover {
        cursor: pointer;
        opacity: 0.5; }
      .banner__buttons--container--share {
        background-color: white;
        color: #d22030;
        display: grid;
