          opacity: 0.5; }
    .banner__buttons--text {
      font-size: 1rem; }
  .banner__createdBy {
    display: grid;
    grid-template-columns: min-content 1fr;
    align-items: center;
    grid-column-gap: 1rem; }
    .banner__createdBy__avatarContainer {
