.normal-text {
  font-size: 2rem; }
  .normal-text--section {
    color: #d22030;
    grid-column: 2;
    text-transform: uppercase;
    font-weight: 500;
    position: relative; }
    .normal-text--section:before, .normal-text--section:after {
      position: absolute;
      content: "";
      height: 0.5rem;
      width: 1rem;
      display: block;
      background-color: #d22030;
      top: 50%;
      transform: translateY(-50%);
      left: -15%; }
    .normal-text--section:after {
      right: -15%;
      left: initial; }

.heading-primary {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 5rem;
  width: 100%;
  padding: 3rem;
  text-align: center; }

.heading-secondary {
  color: #d22030;
  font-size: 4rem;
  width: 100%;
  padding: 1rem;
  text-align: center;
  grid-column: 2; }

.section-header {
  display: grid;
  grid-template-columns: 1fr minmax(min-content, 120rem) 1fr;
  grid-template-rows: min-content min-content;
  justify-items: center;
  margin-bottom: 3rem;
  margin-top: 3rem;
  text-transform: uppercase; }

.seasonal-bundles {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  grid-gap: 2.5rem;
  margin-bottom: 4rem;
  padding: 0rem 4rem; }
  .seasonal-bundles__container {
    display: grid;
    grid-template-rows: 1fr min-content;
    box-shadow: 3px 3px 8px 1px #888888; }
  .seasonal-bundles__img {
    width: 100%; }
  .seasonal-bundles__content {
    color: #d22030;
    padding: 1rem;
    display: grid;
    grid-template-rows: min-content min-content min-content;
    grid-row-gap: 1rem; }
  .seasonal-bundles__actions {
    display: grid;
    grid-template-columns: 1fr 1fr; }
  .seasonal-bundles__buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center; }

.featured-products {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  grid-gap: 3rem;
  padding: 0rem 4rem 4rem 4rem; }
  .featured-products__container {
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
    display: grid;
    grid-template-rows: min-content 20rem min-content; }
  .featured-products__image-container {
    overflow: hidden;
    height: 100%; }
  .featured-products__image {
    width: 100%;
    height: 100%; }
  .featured-products__header {
    background-color: #d22030;
    color: white;
    font-size: 2rem;
    text-align: center;
    font-weight: 500; }
  .featured-products__button {
    background-color: #d22030;
    color: white;
    font-size: 2rem;
    text-align: center; }
